import React, {FC} from "react";
import Avion from "../../models/Avion";
import {IonCard, IonCardContent, IonItem, IonItemDivider, IonList} from "@ionic/react";

interface Props {
    avion: Avion
}

const DetailAvion: FC<Props> = ({avion}) => {
  return (
        <IonCard>
            <IonCardContent>
                <IonItem>
                    <strong>Passager: </strong> {avion.model?.passager}
                </IonItem>
                <IonItem>
                    <strong>Vitesse: </strong> {avion.model?.vitesse}
                </IonItem>
                <IonItem>
                    <strong>Type: </strong> {avion.model?.type.label}
                </IonItem>
                <IonItem>
                    <strong>Categorie: </strong> {avion.model?.type.categorie.nom}
                </IonItem>
            </IonCardContent>
        </IonCard>
  )
}

export default DetailAvion;