import {IonContent, IonFooter, IonPage, IonRouterOutlet, IonTitle} from "@ionic/react";
import MobileHeader from "../components/MobileHeader";
import React, {useEffect, useState} from "react";
import Avion from "../models/Avion";
import AvionService from "../services/AvionServices";
import {Redirect, Route} from "react-router-dom";
import FicheAvion from "../components/avions/FicheAvion";
import AvionList from "../components/avions/AvionList";

interface Props {
    onViewDetail: CallableFunction
}

const Avions: React.FC<Props> = ({onViewDetail}) => {


    const [avions, setAvions] = useState<Array<Avion>>([]);

    function fetchAvions(expired: String | null = null) {
        AvionService.getAll(expired).then((response: any) => {
            setAvions(response.data.data);
        }).catch((error: any) => {
            alert(error)
        });
    }

    useEffect(() => {
        fetchAvions();
    }, [])

    return (
        <IonPage>
            <MobileHeader title={'Avions'} />
            <IonContent>
                <AvionList avions={avions} onFiltered={(expire: String) => fetchAvions(expire)} onViewDetail={onViewDetail} />
            </IonContent>
            <IonFooter>
                <IonTitle>
                    Copyright 2022
                </IonTitle>
            </IonFooter>
        </IonPage>
    )
}

export default Avions;