import axios from "axios";
import {baseUrl} from "./Common";
import Kilometrage from "../models/Kilometrage";

const getAllByAvion = (id: number) => {
    return axios.get<Array<Kilometrage>>(baseUrl+"avions/"+id+"/kilometrages?token="+sessionStorage.getItem("token"));
}

const KilometrageService = {
    getAllByVehicule: getAllByAvion
}

export default KilometrageService;