import {
  IonButton,
  IonContent,
  IonIcon,
  IonItem,
  IonLabel,
  IonList,
  IonListHeader,
  IonMenu,
  IonMenuToggle,
  IonNote,
} from '@ionic/react';

import {useLocation} from 'react-router-dom';
import {airplane, toggle,} from 'ionicons/icons';
import './Menu.css';

interface AppPage {
  url: string;
  iosIcon: string;
  mdIcon: string;
  title: string;
}

const appPages: AppPage[] = [
  {
    title: 'Avions',
    url: '/avions',
    iosIcon: airplane,
    mdIcon: airplane
  }
];

interface Props {
  auth: boolean,
  onLogout: CallableFunction
}

const Menu: React.FC<Props> = ({auth, onLogout}) => {
  const location = useLocation();



  return (
    <IonMenu contentId="main" >
      <IonContent>
        <IonList id="inbox-list">
          <IonListHeader>Gestion Flotte</IonListHeader>
          <IonNote>airmadagascar@gmail.com</IonNote>
          {appPages.map((appPage, index) => {
            return (
              <IonMenuToggle key={index} autoHide={false}>
                <IonItem className={location.pathname === appPage.url ? 'selected' : ''} routerLink={appPage.url} routerDirection="none" lines="none" detail={false}>
                  <IonIcon slot="start" ios={appPage.iosIcon} md={appPage.mdIcon} />
                  <IonLabel>{appPage.title}</IonLabel>
                </IonItem>
              </IonMenuToggle>
            );
          })}
        </IonList>
        {auth ? <IonButton onClick={() => {onLogout()}}>Log out</IonButton> : "Welcome Guest"}
      </IonContent>
    </IonMenu>
  );
};

export default Menu;
