import React, {useEffect, useState} from "react";
import {
    IonButton,
    IonCol,
    IonContent,
    IonGrid,
    IonPage,
    IonProgressBar,
    IonRippleEffect,
    IonRow,
    useIonToast
} from "@ionic/react";
import AvionItem from "./AvionItem";
import KilometrageTable from "../kilometrage/KilometrageTable";
import KilometraService from "../../services/KilometraService";
import Avion from "../../models/Avion";
import {useHistory} from "react-router";
import {usePhotoGallery} from "../../services/PhotoService";
import DetailAvion from "./DetailAvion";
import MobileHeader from "../MobileHeader";

interface Props {
    avion: Avion
}

const FicheAvion: React.FC<Props> = ({avion}) => {

    const {photos,takePhoto} = usePhotoGallery(avion);

    const [loaded, setLoaded] = useState<boolean>(true);

    const [errorToast] = useIonToast();

    const navigate = useHistory();


    useEffect(() => {
        //Cached kilometrages
        if(!avion.kilometrages){
            retrieveKilometrage(avion.id!);
        } else {
            setLoaded(false);
        }
    }, [])

    function retrieveKilometrage(id: number) {
        KilometraService.getAllByVehicule(id).then((response: any) => {
            avion.kilometrages = response.data.data;
            setLoaded(false);
        }).catch((error: any) => {
            errorToast({
                message: error.response.data.error.message,
                duration: 1500
            })
        })
    }

    return (
        <IonPage>
            <MobileHeader title={"Fiche vehicule"} />
            <IonContent>
                <IonGrid>
                    <IonRow>
                        <IonCol size="12" sizeMd="6">
                            <AvionItem avion={avion} onViewDetail={()=>{}} />
                            <DetailAvion avion={avion} />
                            <IonButton onClick={()=>takePhoto()}>
                                Add photo
                                <IonRippleEffect></IonRippleEffect>
                            </IonButton>
                        </IonCol>
                        <IonCol>
                            {
                                loaded ? <IonProgressBar type="indeterminate" /> : <KilometrageTable kilometrages={avion.kilometrages!} />
                            }
                        </IonCol>
                    </IonRow>
                </IonGrid>
            </IonContent>
        </IonPage>
  )
}

export default FicheAvion;