import {FC} from "react";
import Kilometrage from "../../models/Kilometrage";

interface Props {
    kilometrages: Array<Kilometrage>
}

const KilometrageTable: FC<Props> = ({kilometrages}) => {
  return (
    <table>
      <thead>
        <tr>
          <th>Date</th>
          <th>Debut</th>
          <th>Fin</th>
        </tr>
      </thead>
      <tbody>
      {
          kilometrages.map(k => (
              <tr key={k.id}>
                  <td>{k.dates}</td>
                  <td>{k.debut}</td>
                  <td>{k.fin}</td>
              </tr>
          ))
      }
      </tbody>
    </table>
  )
}

export default KilometrageTable;