import { Camera,CameraResultType, CameraSource, Photo} from '@capacitor/camera';
import { Capacitor } from '@capacitor/core';
import { Directory, Filesystem} from '@capacitor/filesystem';
import { isPlatform } from '@ionic/core';
import { save } from 'ionicons/icons';
import { useState } from 'react';
import AvionService from "./AvionServices";
import Avion from "../models/Avion";


const readAsBase64 = async (photo: Photo)=> {
    const response = await fetch(photo.webPath!);
    const blob = await response.blob();
  
    return await convertBlobToBase64(blob) as string;
  }

const convertBlobToBase64 = async (blob: Blob) => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onerror = reject;
    reader.onload = () => {
        resolve(reader.result);
    };
    reader.readAsDataURL(blob);
  });

export function usePhotoGallery(avion: Avion){
    const [photos, setPhotos] = useState<string>();
    const takePhoto = async () =>{
        const capturedPhoto = await Camera.getPhoto({
            resultType: CameraResultType.Uri, 
            source: CameraSource.Camera, 
            quality: 100 
          });
          const base64Data = await readAsBase64(capturedPhoto);

          avion.photos = base64Data
          AvionService.update(avion)
           setPhotos(base64Data);
    }
    return{
        photos,
        takePhoto
    };
}