import React, {FC, useState} from "react";
import {IonButton, IonContent, IonInput, IonItem, IonLabel, IonPage, IonRippleEffect, useIonToast} from "@ionic/react";
import MobileHeader from "../components/MobileHeader";
import AuthenticationServeice from "../services/AuthenticationService";
import {useHistory} from "react-router";

interface Props {
  onLogged: CallableFunction
}

const LoginPage: FC<Props> = ({onLogged}) => {
  const [email, setEmail] = useState<any>("prof@gmail.com");
  const [password, setPassword] = useState<any>("prof");

  const [errorToast] = useIonToast();

  let navigate = useHistory();

  function signIn() {
    AuthenticationServeice.authenticate({
      email: email,
      pwd: password
    }).then((response: any) => {
      onLogged(response.data.data.value);
      navigate.goBack();
    }).catch((error: any) => {
      errorToast({
        message: error.response.data.error.message,
        duration: 1500
      })
    })
  }

  return (
      <IonPage>
        <MobileHeader title={"Log in page"} />
        <IonContent>
          <form>
            <IonItem>
              <IonLabel position="floating">
                Email or Username
              </IonLabel>
              <IonInput
                value={email}
                type="email"
                onIonChange={(e) => setEmail(e.target.value)}
              />
            </IonItem>
            <IonItem>
              <IonLabel position="floating">
                Password
              </IonLabel>
              <IonInput
                value={password}
                type="password"
                onIonChange={(e) => setPassword(e.target.value)}
              />
            </IonItem>
            <IonButton onClick={signIn}>
              Sign in
              <IonRippleEffect></IonRippleEffect>
            </IonButton>
          </form>
        </IonContent>
      </IonPage>
  )
}

export default LoginPage;