import {IonBackButton, IonButton, IonHeader, IonMenuButton, IonTitle, IonToolbar} from "@ionic/react";
import React from "react";

interface Props {
    title: string
}

const MobileHeader: React.FC<Props> = ({title}) => {

    return (
        <IonHeader>
            <IonToolbar>
                <IonMenuButton slot="end" />
                <IonTitle>
                    {title}
                </IonTitle>
                <IonButton slot="start">
                    <IonBackButton />
                </IonButton>
            </IonToolbar>
        </IonHeader>
    )
}

export default MobileHeader;