import axios from "axios";
import Avion from "../models/Avion";
import {baseUrl} from "./Common";

const getAll = (expired: String | null = null) => {
    expired = !expired || expired === "0" ? "" : "?expire="+expired;
    return axios.get<Array<Avion>>(baseUrl+"avions"+expired);
}

const update = (avion: Avion) => {
    return axios.put<any>(baseUrl+`avions/${avion.id}`, avion)
}

const AvionService = {
    getAll,
    update
}

export default AvionService;