import axios from "axios";
import {baseUrl} from "./Common";
import User from "../models/User";

const authenticate = (user: User) => {
    return axios.post<User>(baseUrl+"token", user, {headers: {"Access-Control-Allow-Origin": "*", "Content-Type": "application/json"}})
}

const AuthenticationServeice = {
    authenticate
}

export default AuthenticationServeice;
