import React, {MouseEventHandler} from "react";
import Avion from "../../models/Avion";
import {
    IonButton,
    IonCard, IonCardContent,
    IonCardHeader,
    IonCardSubtitle, IonCardTitle,
    IonContent,
    IonIcon, IonImg,
    IonItem,
    IonLabel,
    IonThumbnail
} from "@ionic/react";
import {car} from "ionicons/icons";

import "../Main.css";
import "./AvionItem.css";

interface Props {
    avion: Avion,
    onViewDetail: MouseEventHandler
}

const AvionItem: React.FC<Props> = ({avion, onViewDetail}) => {

    return (
      <IonCard routerLink={"/avions/"+avion.id} onClick={onViewDetail} >
          <IonImg src={avion.photos!}/>
          <IonCardHeader>
              <IonCardTitle>{avion.matriculation}</IonCardTitle>
          </IonCardHeader>
          <IonCardContent>
              <IonItem>
                  <strong>Model: </strong> {avion.model?.nom}
              </IonItem>
              <IonItem>
                  <strong>Expiration:</strong> {avion.expiration}
              </IonItem>
          </IonCardContent>
      </IonCard>
  )
}

export default AvionItem;