import React, {MouseEventHandler} from "react";
import {IonCol, IonGrid, IonItem, IonRow, IonSelect, IonSelectOption} from "@ionic/react";
import AvionItem from "./AvionItem";
import Avion from "../../models/Avion";

interface Props {
    avions: Array<Avion>,
    onViewDetail: CallableFunction,
    onFiltered: CallableFunction
}

const AvionList: React.FC<Props> = ({avions, onViewDetail, onFiltered}) => {

    return (
      <div>
          <IonItem>
              <IonSelect onIonChange={(e) => onFiltered(e.target.value)} placeholder="Filter by expiration" interface="popover" >
                  <IonSelectOption value="0">All</IonSelectOption>
                  <IonSelectOption value="1">Expired 1</IonSelectOption>
                  <IonSelectOption value="3">Expired 3</IonSelectOption>
              </IonSelect>
          </IonItem>
          <IonGrid>
              <IonRow>
                  {
                      avions.map(v => (
                          <IonCol key={v.id} size="12" sizeMd="4" >
                              <AvionItem avion={v} onViewDetail={() => onViewDetail(v)}  />
                          </IonCol>
                      ))
                  }
              </IonRow>
          </IonGrid>
      </div>
  )
}

export default AvionList;