import {IonApp, IonRouterOutlet, IonSplitPane, setupIonicReact} from '@ionic/react';
import {IonReactRouter} from '@ionic/react-router';
import {Redirect, Route} from 'react-router-dom';
import Menu from './components/Menu';
import Page404 from './pages/404Page';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';
import Avions from "./pages/Avions";
import React, {useState} from "react";
import Authentication from "./models/Authentication";
import LoginPage from "./pages/LoginPage";
import FicheAvion from "./components/avions/FicheAvion";
import Avion from "./models/Avion";

setupIonicReact();

const initialAuth: Authentication = {
  token: null
}

const App: React.FC = () => {

  const initialAvionState: Avion = {
    id: null,
    matriculation: "dfsdgd",
    model: null,
    expiration: null,
    kilometrages: null,
    photos: null
  }

  const [auth, setAuth] = useState<boolean>(false);

  function onLogged(token: string) {
    alert(token);
    sessionStorage.setItem("token", token);
    setAuth(true)
  }

  const [currentAvion, setCurrentAvion] = useState<Avion>(initialAvionState);

  function onViewDetail(v: Avion) {
    setCurrentAvion(v);
  }

  return (
    <IonApp>
      <IonReactRouter>
        <IonSplitPane contentId="main">
          <Menu auth={auth} onLogout={() => {
            sessionStorage.clear();
            setAuth(false)
          }} />
          <IonRouterOutlet id="main">
            <Route path="/" exact={true}>
              <Redirect to="/avions" />
            </Route>
            <Route path="/avions" exact>
              <Avions onViewDetail={onViewDetail}/>
            </Route>
            <Route path="/avions/:id" exact={true}>
              <FicheAvion  avion={currentAvion}/>
            </Route>
            {sessionStorage.getItem("token") == null ? <Redirect to="/auth" from="/avions/:id" /> : <Route />}
            <Route path="/auth">
              <LoginPage onLogged={onLogged} />
            </Route>
            <Route>
              <Page404 />
            </Route>
          </IonRouterOutlet>
        </IonSplitPane>
      </IonReactRouter>
    </IonApp>
  );
};

export default App;
